/**
 * Fires when dom is ready.
 *
 * @param {Function} callback Callback function.
 */
function domReady(callback) {
  if ('loading' === document.readyState) {
    document.addEventListener('DOMContentLoaded', callback);
  } else {
    callback();
  }
}

export default domReady;
